*{
  margin:0;
  padding:0;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.header{
  background-color: black;
  font-weight:bold;
  text-align: center;
  justify-content: center;
  color:white;
  font-size: 30px;
  padding:20px;
  letter-spacing: 3px;
  font-family: Arial, Helvetica, sans-serif;
}
.container{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 80vh;
}
.quiz-start{
   flex-direction: column;
   gap:20px;
}
.flex-center{
  font-size: 20px;
  display: flex;
    align-items: center;
    justify-content: center;
}
.quiz-start button{
  background-color: black;
  color: white;
  padding: 5px 9px;
  font-family: Arial, Helvetica, sans-serif;
  border-radius: 3px;
}
.wrapper{
  border:1px solid grey;
  border-radius: 3px;
  padding:15px;
  min-width: 300px;
  display: flex;
  flex-direction: column;
  gap:30px
}
.options{
    display: flex;
    flex-direction: column;
    gap:10px;
}
.question{
  font-weight:bold;
}
.option input{
  margin-right: 5px;
}
.wrapper button{
  background-color: black;
  color: white;
  padding: 7px;
  border-radius: 3px;
}
.result{
  text-align: center;
}
.result text{
  line-height: 2;
}
.result button{
  background-color: black;
  color: white;
  padding: 3px 7px;
  border-radius: 3px;
  margin-top: 10px;
}